<template>
  <div class="admin-orders">
    <div class="filters">
      <select v-model="statusFilter">
        <option value="">All Status</option>
        <option value="pending">Pending</option>
        <option value="processing">Processing</option>
        <option value="shipped">Shipped</option>
        <option value="delivered">Delivered</option>
        <option value="cancelled">Cancelled</option>
      </select>
      
      <input 
        type="date" 
        v-model="dateFilter"
        placeholder="Filter by date"
      >
    </div>

    <div v-if="loading" class="loading">
      Loading orders...
    </div>

    <div v-else-if="orders.length === 0" class="no-orders">
      No orders found
    </div>

  
    <table v-else class="orders-table">
      <thead>
        <tr>
          <th>Order ID</th>
          <th>Customer</th>
          <th>Date</th>
          <th>Total</th>
          <th>Status</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="order in filteredOrders" :key="order.id">
          <td>#{{ order.id }}</td>
          <td>{{ order.user.name }}</td>
          <td>{{ formatDate(order.date) }}</td>
          <td>${{ order.total.toFixed(2) }}</td>
          <td>
            <select 
              v-model="order.status"
              @change="updateOrderStatus(order.id, order.status)"
            >
              <option value="pending">Pending</option>
              <option value="processing">Processing</option>
              <option value="shipped">Shipped</option>
              <option value="delivered">Delivered</option>
              <option value="cancelled">Cancelled</option>
            </select>
          </td>
          <td>
            <button @click="viewOrderDetails(order)">View Details</button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Order Details Modal -->
    <div v-if="selectedOrder" class="modal">
      <div class="modal-content">
        <h3>Order Details #{{ selectedOrder.id }}</h3>
        
        <div class="customer-info">
          <h4>Customer Information</h4>
          <p>Name: {{ selectedOrder.user.name }}</p>
          <p>Email: {{ selectedOrder.user.email }}</p>
          <p>Phone: {{ selectedOrder.user.phone }}</p>
          <p>Address: {{ selectedOrder.shippingAddress }}</p>
        </div>

        <div class="order-items">
          <h4>Items</h4>
          <div v-for="item in orderItems" :key="item.productId" class="item">
            <span>{{ getProductName(item.productId) }}</span>
            <span>{{ item.quantity }} x ${{ item.price }}</span>
            <span>${{ (item.quantity * item.price).toFixed(2) }}</span>
          </div>
        </div>

        <div class="order-summary">
          <p>Subtotal: ${{ selectedOrder.subtotal.toFixed(2) }}</p>
          <p>Shipping: ${{ selectedOrder.shipping.toFixed(2) }}</p>
          <p>Tax: ${{ selectedOrder.tax.toFixed(2) }}</p>
          <p class="total">Total: ${{ selectedOrder.total.toFixed(2) }}</p>
        </div>

        <button @click="selectedOrder = null">Close</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import products from '@/data/products.json'

const store = useStore()
const statusFilter = ref('')
const dateFilter = ref('')
const selectedOrder = ref(null)
const loading = ref(true)

const orders = computed(() => store.getters['order/getAllOrders'])

const orderItems = computed(() => {
  if (!selectedOrder.value) return []
  return selectedOrder.value.items.map(item => ({
    ...item,
    product: getProductDetails(item.productId)
  }))
})

const getProductDetails = (productId) => {
  return products.products.find(p => p.id === productId)
}

const getProductName = (productId) => {
  const product = getProductDetails(productId)
  return product ? product.name : 'Unknown Product'
}

const filteredOrders = computed(() => {
  if (!orders.value) return []
  
  return orders.value.filter(order => {
    let matches = true
    if (statusFilter.value) {
      matches = matches && order.status === statusFilter.value
    }
    if (dateFilter.value) {
      matches = matches && order.date.startsWith(dateFilter.value)
    }
    return matches
  })
})

const formatDate = (date) => {
  if (!date) return 'N/A'
  return new Date(date).toLocaleDateString()
}

const updateOrderStatus = async (orderId, newStatus) => {
  try {
    await store.dispatch('order/updateOrderStatus', {
      orderId,
      status: newStatus
    })
  } catch (error) {
    console.error('Failed to update order status:', error)
  }
}

const viewOrderDetails = (order) => {
  selectedOrder.value = order
}

onMounted(async () => {
  await store.dispatch('order/fetchOrders')
  loading.value = false
})
</script>

  <style scoped>
.admin-orders {
  padding: var(--spacing-lg);
}

h2 {
  color: var(--text-color);
  font-size: var(--title);
  margin-bottom: var(--spacing-xl);
}

.filters {
  display: flex;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-xl);
  margin-bottom: 20px;
}

.filters select,
.filters input {
  border: 1px solid #e1e1e1;
  border-radius: var(--radius-md);
  min-width: 200px;
  background-color: white;
  padding: 5px;
}

.orders-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  overflow: hidden;
}

.orders-table th {
  background: var(--primary-color);
  color: black;
  padding: var(--spacing-md);
  text-align: left;
  padding: 15px;
}

.orders-table td {
  padding: var(--spacing-md);
  border-bottom: 1px solid #eee;
}

.orders-table tr:hover {
  background-color: var(--gray-bg);
}

.orders-table select {
  padding: 8px;
  border-radius: var(--radius-sm);
  border: 1px solid #ddd;
  width: 140px;
}

.orders-table button {
  background: #3498db;
  padding: 8px 16px;
  border: none;
  border-radius: var(--radius-sm);
  cursor: pointer;
  transition: opacity 0.2s;
}

.orders-table button:hover {
  opacity: 0.9;
}

/* Modal Styles */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: var(--spacing-xl);
  border-radius: var(--radius-lg);
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
}

.modal h3 {
  color: var(--primary-color);
  margin-bottom: var(--spacing-lg);
}

.customer-info, .order-items {
  margin-bottom: var(--spacing-xl);
  padding: var(--spacing-md);
  background: var(--gray-bg);
  border-radius: var(--radius-md);
}

.customer-info h4, .order-items h4 {
  color: var(--text-color);
  margin-bottom: var(--spacing-md);
}

.item {
  display: flex;
  justify-content: space-between;
  padding: var(--spacing-sm) 0;
  border-bottom: 1px solid #eee;
}

.order-summary {
  margin-top: var(--spacing-lg);
  padding-top: var(--spacing-md);
  border-top: 2px solid #eee;
}

.order-summary p {
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--spacing-sm);
}

.total {
  font-weight: bold;
  color: var(--primary-color);
  font-size: var(--text-lg);
}

.modal button {
  background: #3498db;
  color: white;
  padding: var(--spacing-sm) var(--spacing-xl);
  border: none;
  border-radius: var(--radius-md);
  cursor: pointer;
  width: 100%;
  margin-top: var(--spacing-lg);
  transition: opacity 0.2s;
}

.modal button:hover {
  opacity: 0.9;
}
</style>
